    },
    headerButtonAction () {
      if (!this.groupShouldPropose) {
        this.openModal('InvitationLinkModal')
      } else {
        const contractID = this.$store.state.currentGroupId
        sbp('gi.app/group/checkGroupSizeAndProposeMember', { contractID }).catch(e => {
